
import React from 'react';
import ReactDOM from 'react-dom';
import 'antd/dist/antd.css';
import './index.css';
import { Table, Tag, Button, Input, Icon} from 'antd';
import Highlighter from 'react-highlight-words';
import reqwest from 'reqwest';
import moment from 'moment'; // 时间插件  

class App extends React.Component {
  state = {
    loading: false,
    visible: false,
    searchText: '',
    searchedColumn: '',
    data: [],
  };
  getColumnSearchProps = dataIndex => ({
    filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
      <div style={{ padding: 8 }}>
        <Input
          ref={node => {
            this.searchInput = node;
          }}
          placeholder={`Search ${dataIndex}`}
          value={selectedKeys[0]}
          onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
          onPressEnter={() => this.handleSearch(selectedKeys, confirm, dataIndex)}
          style={{ width: 188, marginBottom: 8, display: 'block' }}
        />
        <Button
          type="primary"
          onClick={() => this.handleSearch(selectedKeys, confirm, dataIndex)}
          icon="search"
          size="small"
          style={{ width: 90, marginRight: 8 }}
        >
          Search
        </Button>
        <Button onClick={() => this.handleReset(clearFilters)} size="small" style={{ width: 90 }}>
          Reset
        </Button>
      </div>
    ),
    filterIcon: filtered => (
      <Icon type="search" style={{ color: filtered ? '#1890ff' : undefined }} />
    ),
    onFilter: (value, record) =>
      record[dataIndex]
        .toString()
        .toLowerCase()
        .includes(value.toLowerCase()),
    onFilterDropdownVisibleChange: visible => {
      if (visible) {
        setTimeout(() => this.searchInput.select());
      }
    },
    render: text =>
      this.state.searchedColumn === dataIndex ? (
        <Highlighter
          highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
          searchWords={[this.state.searchText]}
          autoEscape
          textToHighlight={text.toString()}
        />
      ) : (
        text
      ),
  });

  handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
    this.setState({
      searchText: selectedKeys[0],
      searchedColumn: dataIndex,
    });
  };

  handleReset = clearFilters => {
    clearFilters();
    this.setState({ searchText: '' });
  };
  showModal(){
    console.log()
  }
  columns = [
    {
      ...this.getColumnSearchProps('project_name'),

      render: (text,record) => <a onClick={this.showModal} href={'detail/'+record.project_id }>{record.project_id+' '+text}</a>,
      title: '项目名',
      dataIndex: 'project_name',
      sorter: (a, b) => a.name.charCodeAt() - b.name.charCodeAt(),

    },
    {
      title: '状态',
      dataIndex: 'status',
      filters: [
        {
          text: '未开始',
          value: '未开始',
        },
        {
          text: '准备中',
          value: '准备中',
        },
        {
          text: '数据采集',
          value: '数据采集',
        },
        {
          text: '数据标注',
          value: '数据标注',
        },
        {
          text: '暂停',
          value: '暂停',
        },
        {
          text: '完结',
          value: '完结',
        },
      ],
      onFilter: (value, record) => record.status.indexOf(value) === 0,
      sorter: (a, b) => a.status.charCodeAt() - b.status.charCodeAt(),
    },
    {
      title: '创建日期',
      dataIndex: 'create_time',
      sorter: (a, b) =>  new Date(a.create_time.replace(/\-/g, "\/")) - new Date(b.create_time.replace(/\-/g, "\/")),
    },
    {
      title: '标签',
      dataIndex: 'labels',
      sorter: (a, b) => a.labels.length - b.labels.length,
      render: labels => (
        <span>
          {labels.map(tag => (
            <Tag color="blue" key={tag}>
              {tag}
            </Tag>
          ))}
        </span>
      ),
    },
    {
      title: '创建人',
      dataIndex: 'users_found',
      render: users_found => (
        <span>
          {users_found.map(tag => (
            <Tag color="blue" key={tag}>
              {tag}
            </Tag>
          ))}
        </span>
        ),
    },
    {
      title: '管理人',
      dataIndex: 'users_manager',
      render: users_manager => (
        <span>
          {users_manager.map(tag => (
            <Tag color="blue" key={tag}>
              {tag}
            </Tag>
          ))}
        </span>

      ),    
    },
  ];

  // data = [
  //   {
  //     project_id: '1',
  //     project_name: '1号项目wewe',
  //     status:'未开始',
  //     create_time:'2020-02-20',
  //     labels:[],
  //     users_found:'1号用户',
  //     users_manager:'9号用户',
  //   },
  // ];
  componentDidMount() {
    this.fetch();
  }

  fetch = (params = {}) => {
    console.log('params:', params);
    this.setState({ loading: true });
    reqwest({
      url: window.location.href + '/api/project/?format=json',
      // url: 'http://127.0.0.1:8000/aidsp/api/project/?format=json',

      method: 'get',
    }).then(data => {
      console.log(data[0].create_time)

      for (let i in data){
        console.log(i)
        data[i].create_time = data[i].create_time.substring(0,10);
      }
      console.log(data[0].create_time)
      this.setState({data: data});
      this.setState({ loading: false });

    });
  };
  render() {
    return (
      <div>
        <Table columns={this.columns} 
        dataSource={this.state.data} 
        onChange={onChange} 
      rowKey={record => record.project_id}
        loading={this.state.loading}/>
     </div>
)
  }
}
function onChange(pagination, filters, sorter, extra) {
  console.log('params', pagination, filters, sorter, extra);
}

ReactDOM.render(<App />, document.getElementById('container'));
